
@keyframes placeholder {
    0% { transform: translate(-50%, -50%) rotateX(90deg) translateY(50%); }
    50% { transform: translate(-50%, -50%) rotateX(90deg) translateY(150%); }
    100% { transform: translate(-50%, -50%) rotateX(90deg) translateY(50%); }
  }

.game-container {
    position:relative;
    width:100%;
    height:100%;
    background: black;
    overflow: hidden;


    .camera {
        perspective: 300px;
        position: absolute;
        height: 100%;
        width: 100%;
        transform: scale(2);
        transform-style: preserve-3d;
    }

    .scene {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: rotateX(30deg);
        transform-style: preserve-3d;
    }

    .map {
        position: absolute;
        width: 500px;
        height: 500px;
        background: blue;
        transform-style: preserve-3d;
    }


    @import "levelObject/index.scss";
}